/*MARGIN AND PADDING*/
.mb-50 {
  margin-bottom: 50px;
}

.p-20 {
  padding: 20px;
}

.mb-20 {
  margin-bottom: 20px;
}

.mt-50 {
  padding-top: 50px;
}

.has-padding {
  padding: 20px;
}

/*COLORS*/
.is-blue {
  background-color: #00a3e0;
  border: none;
}

.is-blue h1, .is-blue h2, .is-blue > span {
  color: white;
}

.is-red {
  color: red;
}

/*ICON*/
.has-icon {
  margin-left: 0;
}

/*SVG*/
.is-svg {
  max-height: 100% !important;
  height: 100%;
  width: 40%;
}

/*MISC*/
.is-clickable {
  cursor: pointer;
}

.has-no-background {
  background: none;
}

.is-medium-size {
  font-size: 1.5em;
}

.is-light-centered-message {
  font-size: 2em;
  text-transform: uppercase;
  text-align: center;
  display: block;
  margin: 50px;
  color: #e0e0e0;
}

.is-title-centered-message {
  font-size: 2em;
  text-transform: uppercase;
  text-align: center;
  display: block;
  margin-bottom: 50px !important;
  color: #b5b5b5;
}

.is-horizontally-centered {
  align-items: center;
}

.notdeliverable {
  color: #FF3860;
}

.is-not-rounded {
  border-radius: 0;
}

.control.has-icon > .fa {
  color: white;
}

.has-icon {
  margin-left: auto !important;
}

.control.has-icon .input:focus + .fa {
  color: white;
}

input::-webkit-calendar-picker-indicator {
  display: none;
}

input[type="date"]::-webkit-input-placeholder {
  visibility: hidden !important;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  /* display: none; <- Crashes Chrome on hover */
  -webkit-appearance: none;
  margin: 0;
  /* <-- Apparently some margin are still there even though it's hidden */
}

.is-transparant-underline-input {
  border: none;
  background-color: rgba(0, 0, 0, 0.05);
  box-shadow: none;
  border-bottom: 1px solid white;
  border-radius: 0;
  color: white;
  text-align: center;
}

.is-fixed-width-centered-input {
  width: 450px;
  text-align: center;
  margin: 0 auto;
  display: block;
}

/*DEFAULT ANIMATION SETTING*/
.has-animation {
  transition: all .2s;
}

/*ICON HOVER ANIMATION*/
.transitioning-icon {
  overflow: hidden;
  line-height: 2em;
}

.transitioning-icon:hover {
  -webkit-box-shadow: inset 0px -2px 5px -1px rgba(0, 0, 0, 0.31);
  -moz-box-shadow: inset 0px -2px 5px -1px rgba(0, 0, 0, 0.31);
  box-shadow: inset 0px -2px 5px -1px rgba(0, 0, 0, 0.31);
}

.transitioning-icon:hover .has-stacked-icons {
  margin-top: 52px;
}

.blink {
  animation: blink .5s;
  transform-origin: center;
}

.spin-icon:hover .fa {
  transform: rotate(360deg);
  transform-origin: center;
  transition: all 0.3s;
}

.has-stacked-icons {
  display: flex !important;
  flex-direction: column-reverse;
}

.has-stacked-icons .fa {
  padding-top: 10px;
}

.jump-out:hover {
  background: none !important;
  color: white;
  -webkit-box-shadow: 0px 3px 5px -2px rgba(0, 0, 0, 0.52);
  -moz-box-shadow: 0px 3px 5px -2px rgba(0, 0, 0, 0.52);
  box-shadow: 0px 3px 5px -2px rgba(0, 0, 0, 0.52);
  border: 1px solid #eee;
  height: 30px;
  transition: all .3s;
}

.slideInLeft {
  -webkit-animation-duration: .2s;
  animation-duration: .2s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
  -webkit-animation-name: slideInLeft;
  animation-name: slideInLeft;
}

/*JELLO*/
.jello:hover {
  animation: jello .5s;
  transform-origin: center;
}

@keyframes jello {
  0% {
    transform: none;
  }
  10% {
    transform: skewX(-12.5deg) skewY(-12.5deg);
  }
  20% {
    transform: skewX(6.25deg) skewY(6.25deg);
  }
  30% {
    transform: skewX(-3.125deg) skewY(-3.125deg);
  }
  40% {
    transform: skewX(1.5625deg) skewY(1.5625deg);
  }
  50% {
    transform: skewX(-0.78125deg) skewY(-0.78125deg);
  }
  60% {
    transform: skewX(0.390625deg) skewY(0.390625deg);
  }
  70% {
    transform: skewX(-0.1953125deg) skewY(-0.1953125deg);
  }
  100% {
    transform: none;
  }
}

@keyframes blink {
  0% {
    opacity: 0;
    transform: scale(1);
  }
  25% {
    opacity: 1;
  }
  50% {
    opacity: 0;
    transform: scale(1.7);
  }
  100% {
    opacity: 0;
    transform: scale(1);
  }
}

@-webkit-keyframes slideInLeft {
  from {
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0);
    visibility: visible;
  }
  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

@keyframes pulse {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

